<template>
  <div class="row">
    <!-- coluna 1 -->
    <div class="col-8">
      <h2>Filmes</h2>

      <ul class="list-group list-group-flush">
        <FilmesListaIten
          v-for="filme in filmes"
          :key="filme.id"
          :filme="filme"
          :class="aplicarClasseAtiva(filme)"
          @selecionarFilme="filmeSelecionado = $event"
        />

        <div class="input-group mb-3" style="padding: 20px">
          <input
            type="text"
            class="form-control"
            placeholder="Insira o nome do filme"
            name="novoFilme"
            v-model="novoFilme"
          />

          <input
            type="text"
            class="form-control"
            placeholder="Insira o ano"
            name="novoAno"
            v-model="novoAno"
          />

          <button class="btn btn-success float-right" @click="adicionar()">
            Adicionar
          </button>
        </div>
      </ul>
    </div>

    <!-- coluna 2 -->
    <div class="col-4">
      <FilmesListaItenInfo
        v-if="!editar"
        :filme="filmeSelecionado"
        @editarFilme="editarFilme"
      />

      <FilmesListaItenEditar v-else :filme="filmeSelecionado" />
    </div>
  </div>
</template>

<script>
import { eventBus } from "./../main";
import FilmesListaIten from "./FilmesListaIten.vue";
import FilmesListaItenEditar from "./FilmesListaItenEditar.vue";
import FilmesListaItenInfo from "./FilmesListaItenInfo.vue";

export default {
  components: {
    FilmesListaIten,
    FilmesListaItenEditar,
    FilmesListaItenInfo,
  },
  data() {
    return {
      filmes: [
        {
          id: 1,
          titulo: "Vingadores: Guerra Infinita",
          ano: 2018,
        },
        {
          id: 2,
          titulo: "Homem Formiga e a Vespa",
          ano: 2018,
        },
        { id: 3, titulo: "Pantera Negra", ano: 2018 },
        { id: 4, titulo: "Deadpool 2", ano: 2018 },
      ],
      filmeSelecionado: undefined,
      editar: false,
      novoFilme: "",
      novoAno: "",
    };
  },
  methods: {
    aplicarClasseAtiva(filmeIterado) {
      return {
        active:
          this.filmeSelecionado && this.filmeSelecionado.id === filmeIterado.id,
      };
    },
    editarFilme(filme) {
      this.editar = true;
      this.filmeSelecionado = filme;
    },
    atualizarFilme(filmeAtualizado) {
      const indice = this.filmes.findIndex(
        (filme) => filme.id === filmeAtualizado.id
      );
      this.filmes.splice(indice, 1, filmeAtualizado);
      this.filmeSelecionado = undefined;
      this.editar = false;
    },
    adicionar() {
      const VerificaCampoVazio = this.VerificaCampoVazio();
      if (!VerificaCampoVazio) {
        return window.alert(
          "Erro! Verifique se todos dados foram inseridos adequadamente!"
        );
      }
      this.filmes.push({
        titulo: this.novoFilme,
        ano: this.novoAno,
      });
      this.novoFilme = "", this.novoAno = "";
    },
    VerificaCampoVazio() {
      if (this.novoFilme == "" || this.novoAno == "") {
        return false;
      }
      return true;
    },
  },
  created() {
    eventBus.$on("selecionarFilme", (filmeSelecionado) => {
      this.filmeSelecionado = filmeSelecionado;
    });

    eventBus.$on("atualizarFilme", this.atualizarFilme)
  },
  
};
</script>
